/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
html {
  line-height: 1;
  font-size: 14px;
}
body {
  width: 100%;
  font-family: 'ProximaNova', sans-serif;
  color: #000;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: black;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
textarea {
  resize: none;
}
section {
  position: relative;
  padding: 40px 0;
}
input,
select,
button {
  outline: none;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  width: 100%;
  max-width: 970px;
  margin: 0 auto;
  padding: 0 15px;
}
.header {
  padding: 0 70px;
}
.header__wrap {
  padding: 5px 0 30px;
}
.header-logo {
  display: inline-block;
}
.header-logo__link {
  display: block;
}
.header-flex {
  display: inline-flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 450px;
  margin-left: 110px;
  margin-top: 40px;
  vertical-align: top;
}
.header-profile__top {
  margin-bottom: 20px;
}
.header-profile__name {
  display: inline-block;
  font-size: 35px;
  color: #262626;
}
.header-profile__datastart {
  display: inline-block;
  margin-left: 20px;
  padding: 0 10px;
  line-height: 30px;
  color: #4090db;
  border: 1px solid #4090db;
  border-radius: 5px;
  font-family: 'HelveticaNeue', sans-serif;
  vertical-align: bottom;
}
.header-profile__status {
  display: flex;
}
.header-profile__info {
  font-size: 17px;
  margin-right: 30px;
}
.header-profile__info:last-child {
  margin-right: 0;
}
.header-profile__info span {
  font-weight: bold;
}
.header-date {
  width: 100%;
  margin-top: 25px;
}
.header-date label {
  font-size: 17px;
  font-family: 'Arial', sans-serif;
}
.header-date__start {
  display: inline-block;
  position: relative;
  width: 100%;
  max-width: 180px;
  margin-left: 15px;
}
.header-date__start input {
  line-height: 30px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding-left: 10px;
}
.header-date__end {
  display: inline-block;
  position: relative;
  width: 100%;
  max-width: 180px;
  margin-left: 25px;
}
.header-date__end input {
  line-height: 30px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding-left: 10px;
}
.header-date__close {
  position: absolute;
  top: 1px;
  right: 32px;
  background-color: #ebebeb;
  border-left: 1px solid #ccc;
  padding: 9px;
  cursor: pointer;
}
.header-date__close i {
  background-image: url(../img/sprite/sprite.png);
  background-position: -35px -30px;
  width: 14px;
  height: 14px;
  display: block;
}
.header-date__range {
  position: absolute;
  top: 1px;
  right: 1px;
  background-color: #ebebeb;
  border-left: 1px solid #ccc;
  padding: 6px;
  cursor: pointer;
}
.header-date__range i {
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px -30px;
  width: 18px;
  height: 20px;
  display: block;
}
.header-date .qs-datepicker-container {
  border-color: #b8b8b8;
  margin-top: 3px;
  overflow: inherit;
}
.header-date .qs-datepicker-container:before,
.header-date .qs-datepicker-container::after {
  content: '';
  position: absolute;
  left: 20px;
  top: -10px;
  border: 5px solid transparent;
  border-bottom-color: transparent;
  border-bottom-style: solid;
  border-bottom-width: 10px;
  border-bottom: 5px solid #b8b8b8;
}
.header-date .qs-datepicker-container:after {
  border-bottom: 5px solid #fff;
  top: -9px;
}
.header-date .qs-datepicker .qs-controls {
  background: none;
}
.header-date .qs-datepicker .qs-square.qs-day {
  color: #444;
}
.header-date .qs-datepicker .qs-square.qs-active.qs-current {
  background: #4090db;
  color: #fff;
  text-decoration: none;
  border: none;
}
.header-date .qs-datepicker .qs-square.qs-active {
  background: #4090db;
  color: #fff;
}
.header-date .qs-datepicker .qs-square.qs-current {
  font-weight: normal;
  background: inherit;
  color: inherit;
}
.header-date .qs-datepicker .qs-square.qs-disabled {
  background: inherit;
  color: inherit;
}
.header-date .qs-arrow.qs-left::after {
  border: none;
  display: block;
  background-image: url(../img/sprite/sprite.png);
  background-position: -38px -19px;
  width: 7px;
  height: 6px;
}
.header-date .qs-arrow.qs-right::after {
  border: none;
  display: block;
  background-image: url(../img/sprite/sprite.png);
  background-position: -45px -19px;
  width: 7px;
  height: 6px;
}
.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.gallery__wrap {
  background-color: #fafafa;
  box-shadow: inset 0 4px 16px -4px rgba(0, 0, 0, 0.22);
}
.gallery__item {
  width: 100%;
  max-width: 293px;
  position: relative;
  margin: 0 5px 30px;
}
.gallery__item--video {
  position: relative;
}
.gallery__item--video:before {
  content: '';
  position: absolute;
  top: 15px;
  right: 10px;
  background-image: url(../img/sprite/sprite.png);
  background-position: 0px 0px;
  width: 38px;
  height: 30px;
  z-index: 1000;
}
.gallery__img {
  position: relative;
}
.gallery__img:after {
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
}
.gallery__img img {
  width: 100%;
}
.gallery__info {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 10px;
  color: #ffffff;
  font-size: 17px;
}
.gallery__row {
  display: flex;
  margin-top: 20px;
  padding: 0 10px;
}
.gallery__row:last-child {
  justify-content: center;
}
.gallery__day {
  min-width: 80px;
  margin-right: 20px;
}
.gallery__like {
  min-width: 80px;
  margin-right: 20px;
  line-height: 20px;
  font-weight: bold;
}
.gallery__like--icon {
  position: relative;
  padding-left: 28px;
}
.gallery__like--icon:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../img/sprite/sprite.png);
  background-position: -38px 0px;
  width: 21px;
  height: 19px;
}
.gallery__comment {
  font-weight: bold;
  line-height: 20px;
}
.gallery__comment--icon {
  position: relative;
  padding-left: 25px;
}
.gallery__comment--icon:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../img/sprite/sprite.png);
  background-position: -18px -30px;
  width: 17px;
  height: 17px;
}
.gallery-button {
  padding: 10px 30px;
  background: none;
  font-size: 12px;
  text-transform: uppercase;
  border: 1px solid #444;
  border-radius: 20px;
  cursor: pointer;
}
.gallery-button__wrap {
  text-align: center;
}
@media only screen and (max-width: 938px) {
  .gallery {
    justify-content: center;
  }
  .gallery__item {
    margin: 0 15px 30px;
  }
}
@media only screen and (max-width: 818px) {
  .header {
    text-align: center;
  }
  .header-flex {
    margin: 30px 0 0;
    width: 100%;
    justify-content: center;
  }
  .header-profile__status {
    justify-content: center;
  }
}
@media only screen and (max-width: 610px) {
  .header {
    padding: 0 20px;
  }
  .header-date__end {
    margin-left: 53px;
    margin-top: 15px;
  }
}
@media only screen and (max-width: 544px) {
  .header-profile__datastart {
    margin: 20px auto 0;
    display: block;
    width: 100%;
    max-width: 150px;
  }
}
